import PressCountDown from '../press-count-down/press-count-down';
import { SWITCH_TYPE, BUTTON_TIP_TYPE_AMP } from './config';
import utils from '../common/utils/utils';

export default {
  name: 'PressScheduleCardButton',
  components: {
    PressCountDown,
  },
  props: {
    // 
    buttonType: {
      type: String,
      default: '',
    },
    buttonText: {
      type: String,
      default: '',
    },
    // 
    showButtonCountdown: {
      type: String,
      default: '',
    },
    // width: 1.44  /  4;
    // height: 0.56  / 0.72;
    // 
    buttonStyle: {
      type: Object,
      default: () => ({
        width: 4,
        height: 0.72,
      }),
    },
    // 
    showButtonTip: {
      type: Boolean,
      default: false,
    },
    // 
    buttonTipType: {
      type: String,
      default: BUTTON_TIP_TYPE_AMP.TEXT,
    },
    // 
    // 
    // 
    buttonTip: {
      type: String,
      default: '',
    },
    buttonCountDown: {
      type: Number,
      default: 0,
    },
    buttonAvatarList: {
      type: Array,
      default: () => ([
        // {
        //   head: 'https://thirdqq.qlogo.cn/g?b=oidb&k=HFh953g22kPgQny1h7KITQ&kti=ZJJzkQAAAAE&s=640&t=1556998035',
        //   isGreen: true,
        // },
        // {
        //   head: 'https://thirdqq.qlogo.cn/g?b=oidb&k=HFh953g22kPgQny1h7KITQ&kti=ZJJzkQAAAAE&s=640&t=1556998035',
        //   isYellow: true,
        // },
        // {
        //   head: 'https://thirdqq.qlogo.cn/g?b=oidb&k=HFh953g22kPgQny1h7KITQ&kti=ZJJzkQAAAAE&s=640&t=1556998035',
        //   isYellow: true,
        // },
        // {
        //   head: 'https://thirdqq.qlogo.cn/g?b=oidb&k=HFh953g22kPgQny1h7KITQ&kti=ZJJzkQAAAAE&s=640&t=1556998035',
        // },
      ]),
    },
  },
  emits: [
    'click',
    'finish',
  ],
  data() {
    return {
      SWITCH_TYPE,
      BUTTON_TIP_TYPE_AMP,
    };
  },
  computed: {
    // 
    extraButtonClass() {
      return utils.bem2('card-button__main', {
        green: this.buttonType === 'green',
        gray: this.buttonType === 'gray',
      });
    },
  },
  mounted() {
  },
  updated() {
  },
  methods: {
    finish() {
      if (this.buttonCountDown <= 0) return;
      this.$emit('finish');
    },
    onClickButton() {
      this.$emit('click');
    },
  },
};
