import PressCountDown from '../press-count-down/press-count-down';

export default {
  name: 'PressScheduleCardCountDown',
  components: {
    PressCountDown,
  },
  props: {
    countdownTime: {
      type: Number,
      default: 0,
      required: true,
    },
    forceShowDay: {
      type: Boolean,
      default: false,
    },
    showUnit: {
      type: Boolean,
      default: false,
    },
  },
  emits: [
    'change',
    'finish',
  ],
  data() {
    return {
      timeData: {},
    };
  },
  computed: {
    isContainDays() {
      if (this.forceShowDay) return true;

      return this.countdownTime > 24 * 60 * 60 * 1000;
    },
  },
  methods: {
    onChange(val) {
      this.timeData = val;
      this.$emit('change', val);
    },
    finish() {
      //  0
      if (this.countdownTime <= 0) return;
      this.$emit('finish');
    },
  },
};
